body {
    background-color: #e6dcbb;
    overflow-x: hidden;
  }
  .HomePageTitle {
    text-align: center;
  }
  .author {
    margin-left: 5%;
  }
  .Button {
    position: relative;
    margin-left: 20px;
  }
  .cardTitle {
    font-size: 20px !important;
  }
  .iconButton {
    position: relative;
    margin-left: 30px !important;
  }
  .ButtonHover:hover {
    border-bottom: 2px solid #ff1744;
  }
  .ShareButton {
    margin-left: 100px;
  }
  .aboutTitle {
    position: relative;
    text-align: center;
    margin-top: 50px !important;
  }
  .visitGitHub {
    text-decoration: none;
    color: #3f51b5;
    font-weight: bold;
    cursor: pointer;
  }
  .visitGitHub:hover {
    border-bottom: 2px solid #ff1744;
  }
  .App {
    text-align: center;
  }
  
  .App-logo {
    height: 40vmin;
    pointer-events: none;
  }
  
  @media (prefers-reduced-motion: no-preference) {
    .App-logo {
      animation: App-logo-spin infinite 20s linear;
    }
  }
  
  .App-header {
    background-color: #282c34;
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: calc(10px + 2vmin);
    color: white;
  }
  
  .App-link {
    color: #61dafb;
  }
  
  @keyframes App-logo-spin {
    from {
      transform: rotate(0deg);
    }
    to {
      transform: rotate(360deg);
    }
  }